body {
  &.dark {
    .neko-display-container {
      .neko-display-loading {
        background-color: #212121;
      }
    }
  }
  .neko-display-container {
    height: 100%;
    .neko-display-loading {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      z-index: 999;
    }
    .neko-display-image {
      max-height: 600px;
      max-width: 100%;
    }
  }
}
